:root {
  --main-color: #f9deff;
  --text-color: #971bbd;
  --name-color: #66198a;
}

@media print {
  * {
    -webkit-print-color-adjust: exact !important;
    print-color-adjust: exact !important;
  }

  .profession_container, .block_title, .avatar_img {
    background-color: var(--main-color) !important;
    border-color: var(--main-color) !important;
  }

  .profession:before, .profession_container:before, .horizon_line-1:before, .horizon_line-2:before, .wide_horizon_line:before {
    background-color: var(--main-color) !important;
  }
}

.body {
  color: var(--name-color);
  width: 21cm;
  height: 29.7cm;
  margin: 0 auto;
  font-family: Inter, sans-serif;
}

.header {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding: 2rem 0;
  display: flex;
}

.name {
  color: var(--name-color);
  margin: 0;
  font-size: 2.2rem;
  font-weight: 700;
}

.profession {
  color: var(--text-color);
  margin: 0;
  font-size: 1.2rem;
  font-weight: 700;
}

.profession_container {
  background-color: var(--main-color);
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  padding: .2rem 1rem;
  display: flex;
}

.profession_container:before {
  content: "";
  background-color: var(--main-color);
  width: 4px;
  height: 20px;
  display: block;
  position: absolute;
  top: 25px;
}

.avatar {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.avatar_img {
  border: 20px solid var(--main-color);
  border-radius: 50%;
  width: 25%;
  height: 25%;
  display: block;
}

.main {
  justify-content: space-between;
  gap: 2rem;
  display: flex;
}

.side {
  width: 48%;
}

.block {
  margin-bottom: 2rem;
  padding: 0 1rem;
}

.block_title {
  background-color: var(--main-color);
  color: var(--text-color);
  border-radius: 10px;
  margin: 0 0 1rem;
  padding: 1rem;
  font-size: 1.1rem;
}

.title, .subtitle, .date, .story, .skill, .contact {
  font-size: .8rem;
}

.text_right {
  text-align: right;
}

.job {
  width: 60%;
}

.block_to_right {
  flex-direction: column;
  align-items: flex-end;
  gap: 2rem;
  width: 100%;
  display: flex;
}

.story {
  text-indent: 10px;
  text-align: justify;
  margin: 0;
}

.title, .subtitle, .date {
  margin: 0 0 3px;
}

.speciality_container {
  margin-bottom: 1rem;
}

.skill {
  margin: 5px 0;
}

.indentation {
  margin-bottom: 7px;
}

.profession:before {
  content: "";
  background-color: var(--main-color);
  width: 4px;
  height: 795px;
  display: block;
  position: absolute;
  top: 195px;
  left: 50%;
}

.horizon_line-1:before, .horizon_line-2:before {
  content: "";
  background-color: var(--main-color);
  width: 35px;
  height: 4px;
  display: block;
  position: absolute;
  top: 23px;
  left: -30px;
}

.wide_horizon_line:before {
  content: "";
  background-color: var(--main-color);
  width: 80px;
  height: 4px;
  display: block;
  position: absolute;
  top: 23px;
  left: -70px;
}

.profession, .profession_container, .horizon_line-1, .horizon_line-2, .wide_horizon_line {
  position: relative;
}
